<template>
	<view class="box">
		<view class="back" @click="handleBack">
			<image src="@/static/img/icon_back@3x.png" mode=""></image>
		</view>
		<view class="order">
			<swiper class="swiper" :indicator-dots="false" :autoplay="true" :interval="3000" :duration="1000">
				<swiper-item v-for="(item,index) in orderData.ImageList||[]">
					<image :src="`https://farm.juzhentech.com${item.Image}`" mode=""></image>
				</swiper-item>
			</swiper>
			<view class="address-tinme">
				<view class="boxs">
					<view class="title">{{orderData.Title}}</view>
					<view class="three">
						<!-- <uni-icons type="wallet-filled" color="#00D08F"></uni-icons>-->
						<image src="@/static/img/icon_date@3x.png" mode="aspectFit"></image>
						<view class="text">{{orderData.ActivityTime}}</view>
					</view>
					<view class="three">
						<!-- <uni-icons type="heart" color="#00D08F"></uni-icons> -->
						<image src="@/static/img/icon_location@3x.png" mode="aspectFit"></image>
						<text class="text">{{orderData.ActivityAddress}}</text>
					</view>
				</view>
			</view>
			<view class="organizer">
				<view class="title">组织者</view>
				<view class="title-text">
					<view class="text-left">
						<image :src="`https://farm.juzhentech.com${orderData.Image}`" mode=""></image>
						<text>{{orderData.NickName}}</text>
					</view>
					<view class="text-right">
						<!-- <image src="@/static/img/icon_wechat-line@3x.png" mode=""></image>
						<image src="@/static/img/icon_call@3x.png" mode=""></image> -->
					</view>
				</view>
			</view>
			<view class="registered">
				<view class="title">
					<view class="">已报名</view>
					<!-- <view class="" @click="handleUsers">查看详情</view> -->
				</view>
				<view class="title-text">
					<view class="title-text-left">
						<view class="imgs">
							<image v-if="orderData.SignList.length>0" v-for="(item,index) in orderData.SignList"
								:key="index" :style="[{'margin-left': (index*35)+'rpx'}]"
								:src="`https://farm.juzhentech.com${item.Image}`" mode="">
							</image>
							<text v-else>暂无</text>
						</view>
					</view>
					<view class="text-right">
						<text>{{orderData.SignList.length}}</text>
						<text>/</text>
						<text>{{orderData.SignCount}}</text>
					</view>
				</view>
			</view>
			<view class="introduction">
				<view class="title">活动介绍</view>
				<view class="content">{{orderData.Content||'暂无'}}</view>
			</view>
		</view>

		<view class="bottom">
			<view class="forward">
				<view class="collection">
					<image :src="orderData.IsCollection?trueCollection :falseCollection" @click="handleCollection"
						mode=""></image>
					<view class="" :style="{color: orderData.IsCollection?'#00D08F':'#000'}">
						{{orderData.IsCollection?'已收藏':'收藏'}}
					</view>
				</view>
				<view class="collection">
					<!-- <view class="">分享</view> -->
					<button class="share" open-type="share">
						<image src="@/static/img/icon_share-l@2x.png" mode=""></image>
						<view class="">分享</view>
					</button>
				</view>
			</view>

			<view class="activeity" v-if="orderData.IsMy">
				<view class="activity-btn" @click="handleCancalActivity">取消活动</view>
				<view class="activity-btn" @click="handleSignDetail">活动签到</view>
			</view>
			<view class="" v-else>
				<view class="btn" v-if="orderData.IsSign">已报名</view>
				<view class="btn" v-else @click="eventRegistration">活动报名</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		GetActivityGetDetailAjax,
		GetCancelActivityAjax,
		GetActivitySignDetailAjax,
		GetActivityCollionAjax
	} from '@/apis/api/modules/activity'
	import falseCollection from '@/static/img/icon_like@3x.png'
	import trueCollection from '@/static/img/icon_like@3x_1.png'
	export default {
		data() {
			return {
				activityId: null,
				orderData: null,
				falseCollection,
				trueCollection
			}
		},
		onLoad(e) {
			console.log(e.activityId)
			this.activityId = e.activityId
			this.getData()
		},
		/**
		 * 分享
		 */
		onShareAppMessage(res) {
			return {
				title: this.orderData.Title,
				// path: '/pages/activity/index'
				path: `/pages/activity/event-details/index?activityId=${this.activityId}`
			}
		},
		methods: {
			async getData() {
				const result = await GetActivityGetDetailAjax({
					ActivityID: this.activityId,
					token: true
				})
				this.orderData = result.Result
			},
			/**
			 * 收藏
			 */
			async handleCollection() {
				const result = await GetActivityCollionAjax({
					ActivityID: this.activityId,
					token: true
				})
				uni.showToast({
					title: result.Result,
					icon: 'none'
				})
				this.getData()
			},
			/**
			 * 返回上一页
			 */
			handleBack() {
				uni.navigateBack({
					delta: 1
				})
			},
			/**
			 * 活动报名
			 */
			eventRegistration() {
				uni.navigateTo({
					url: `/pages/activity/event-registration/index?activityId=${this.activityId}`
				})
			},
			/**
			 * 取消活动
			 */
			async handleCancalActivity() {
				await GetCancelActivityAjax({
					ActivityID: this.activityId,
					token: true
				})
				uni.showToast({
					title: '取消成功！',
					icon: 'success'
				})
				setTimeout(() => {
					uni.navigateBack({
						delta: 1
					})
				}, 1000)
			},
			async handleSignDetail() {
				uni.navigateTo({
					url: `/pages/activity/sign-detail/index?activityId=${this.orderData.ActivityID}`
				})
			},
			/**
			 * 已报名用户查看详情
			 */
			handleUsers() {
				let items = this.orderData.SignList;
				uni.setStorageSync("userList", items);
				uni.navigateTo({
					url: '/pages/activity/handle-users/index'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		display: flex;
		flex-direction: column;
		height: 100%;
		box-sizing: border-box;
		background-color: #F7F7F7;
		// min-height: 100%;
	}

	.back {
		position: absolute;
		top: 66rpx;
