		width: 100%;
		box-sizing: border-box;
		padding: 30rpx;
		background: linear-gradient(180deg, #EEFFF6 0%, #FFFFFF 100%);
		box-shadow: 0px -10rpx 16rpx 0px rgba(0, 0, 0, 0.05);
