		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 120rpx;
		background: #FFFFFF;
		padding: 16rpx 30rpx;
		box-sizing: border-box;
		box-shadow: 0px -4rpx 8rpx 0rpx rgba(0, 0, 0, 0.05);
		border-radius: 30rpx 30rpx 0px 0px;

		.forward {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}

		.collection {
			width: 112rpx;
			text-align: center;

			// .share {
			// 	border: none;
			// }
			// button {
			// 	border-style: none;
			// 	border-radius: 0rpx;
			// 	margin: 0rpx;
			// 	padding: 0rpx;
			// 	background-color: transparent;
			// }
			button::after {
